import React, { useState, useEffect, useLayoutEffect } from "react";
import { Typography, Image, Row, Col, Card, Carousel } from "antd";
import {
  ClockCircleOutlined,
  PushpinOutlined,
  UserOutlined,
} from "@ant-design/icons";
import image from "../imgs/church-wide.jpg";
import data from "../data/EventByServiceData";
import Banner from "../components/Banner";
// import Carousel from "react-gallery-carousel";
// import "react-gallery-carousel/dist/index.css";
// Event Page, with props given from parent component ChurchWide.
function EventByService(props) {
  const { Title } = Typography;
  var styles = {
    contentStyle: {
      height: "160px",
      color: "black",
      lineHeight: "160px",
      textAlign: "center",
      background: "#F2F2F2",
    },
  };
  //   const {
  //     location: { match.params.id },
  //   } = props;
  var images = [
    "http://www.trbc.org.sg/images/Images/Worship2.jpg",
    "http://www.trbc.org.sg/images/Images/Worship2.jpg",
  ];

  const id = props.match.params.id;
  const findData = () => {
    var result = {};
    data.forEach((element) => {
      if (element.key == id) {
        result = element;
      }
    });
    return result;
  };
  const [dataToInsert, setDataToInsert] = useState(findData());
  useEffect(() => {
    setDataToInsert(findData());
  }, [props.match.params.id]);

  return (
    <>
      <Banner name={dataToInsert.key} image={image}></Banner>
      <div className="main-container">
        <br />
        <br />
        <Row gutter={[60, 60]}>
          <Col sm={24} lg={12}>
            {" "}
            <Image
              className="services-pages-img"
              src="error"
              fallback="data:image/png;base64,"
            />
            {/* <Carousel
            
              images={images.map((i) => {
                return {
                  src: i,
                  sizes: '(max-width: 100px) 100px',
                };
              })}
              shouldMaximizeOnClick={true}
              playIcon={false}
              hasIndexBoard={false}
            /> */}
          </Col>
          <Col sm={24} lg={12}>
            <Title style={{ textAlign: "left" }}>{dataToInsert.title}</Title>
            <hr align="left" className="thick-line"></hr>

            <div
              dangerouslySetInnerHTML={{ __html: dataToInsert.writeup }}
              style={{ textAlign: "left" }}
            ></div>
            <Card
              bodyStyle={{
                backgroundColor: "#F2F2F2",
                textAlign: "left",
              }}
            >
              <p>
                <ClockCircleOutlined style={{ color: "#fa8c16" }} /> {"    "}
                {dataToInsert.time}
              </p>
              <p>
                <PushpinOutlined style={{ color: "#fa8c16" }} /> {"    "}
                {dataToInsert.location}
              </p>
            </Card>
          </Col>
        </Row>
      </div>
      {/* Need to find a better carousel, to change */}
      <Carousel autoplay>
            <div>
              <h3 style={styles.contentStyle}>1</h3>
            </div>
            <div>
              <h3 style={styles.contentStyle}>2</h3>
            </div>
            <div>
              <h3 style={styles.contentStyle}>3</h3>
            </div>
            <div>
              <h3 style={styles.contentStyle}>4</h3>
            </div>
          </Carousel>
    </>
  );
}

export default EventByService;
