import React from "react";
import { Typography, Image, Row, Col, Card } from "antd";
import {
  ClockCircleOutlined,
  PushpinOutlined,
  UserOutlined,
} from "@ant-design/icons";
import Banner from "../components/Banner";
import image from "../imgs/church-wide.jpg";
import Carousel from 'react-gallery-carousel';
import 'react-gallery-carousel/dist/index.css';
// Event Page, with props given from parent component ChurchWide.
function Event(props) {
  const { Title } = Typography;
  var styles = {
    contentStyle: {
      height: "160px",
      color: "black",
      lineHeight: "160px",
      textAlign: "center",
      background: "#F2F2F2",
    },
  };
  const images = [900, 800, 700, 600, 500].map((size) => ({
    src: `https://cdn.britannica.com/12/61412-050-A4FCE8CE/Ruins-pagodas-shrines-Buddhist-Myanmar-Pagan.jpg`
  }));
  const {
    location: { state },
  } = props;
  console.log(props)
  console.log(state);

  return (
    <>
      <Banner name={state.type} image={image}></Banner>
      <div className="main-container">
        <br />
        <br />
        <Row gutter={[60, 60]}>
          <Col sm={24} lg={12}>
            {" "}
            {/* <Image
              src="https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png"
              fallback="data:image/png;base64,"
            /> */}
            <Carousel images={images} shouldMaximizeOnClick={true} playIcon={false} hasIndexBoard={false}/>
          </Col>
          <Col sm={24} lg={12}>
            <Title style={{ textAlign: "left" }}>{state.key}</Title>
            <hr align="left" className="thick-line"></hr>
            <p style={{ textAlign: "left" }}>{state.writeup}</p>
            {state.type == "Community" |state.type ==  "Interest Groups" |state.type == "Children"? (
              <Card
                bodyStyle={{
                  backgroundColor: "#F2F2F2",
                  textAlign: "left",
                }}
              >
                <p>
                  <ClockCircleOutlined style={{ color: "#fa8c16" }} /> {"    "}
                  {state.time}
                </p>
                <p>
                  <PushpinOutlined style={{ color: "#fa8c16" }} /> {"    "}
                  {state.location}
                </p>
                <p>
                  <UserOutlined style={{ color: "#fa8c16" }} />
                  {"   "}
                  {state.leader}
                </p>
              </Card>
            ) : null}
          </Col>
        </Row>
      </div>

    </>
  );
}

export default Event;
