import React from "react";
import Banner from "../components/Banner";
import image from "../imgs/exterior.jpg";
import { Row, Col, Image } from "antd";
function Vision() {
  return (
    <div id="home-container">
      <Banner name="Mission and Vision" image={image}></Banner>
      <div id="mission-vision-container">
        <Row gutter={16}>
          <Col sm={24} md={12}>
            <Image
            style={{width:"400px", height: "auto"}}
              src="error"
              fallback="data:image/png;base64,"
            />
          </Col>
          <Col sm={24} md={12}>
            <div>
              <h1>Mission</h1>
              <hr id="mv-thickline" />
              <p>
                To Be Disciples Moving as One Body in Christ, Bearing Fruit and
                Being the Salt and Light of the World.
              </p>
            </div>
            <div id="mv-vision-container">
              <h1>Vision</h1>
              <hr id="mv-thickline" />
              <p>One Church Growing Inwardly and Outwardly</p>
            </div>
          </Col>
        </Row>

      
      </div>
    </div>
  );
}

export default Vision;
