import React from "react";
import { Button } from "antd";
import {
  Typography,
  Image,
  Row,
  Col,
  Card,
  Form,
  Checkbox,
  Input,
  Select,
} from "antd";
import image from '../imgs/church-wide.jpg'
import Banner from '../components/Banner';
function ImNew() {
  const { Title } = Typography;
  const { Option } = Select;
  return (
    <div>
            <Banner name= "I'm New" image={image}>

</Banner>
      <div className="main-container">
        <br />
        <br />
        <Row gutter={[60, 60]}>
          <Col sm={11} lg={11}>
            {" "}
            <Image
              width={300}
              height={300}
              src="error"
              fallback="data:image/png;base64,"
            />
          </Col>
          <Col sm={11} lg={12}>
            <h2 style={{ textAlign: "left" }}>
              The best place to start? Join us for a worship service.
            </h2>
            <hr align="left" className="thick-line"></hr>
            <p style={{ textAlign: "left" }}>
              If you’re new to the area, new to church, or just looking for a
              new start, our Sunday morning worship services offer you the
              opportunity to build your faith in God and connect with new
              people.
            </p>
            <Button type="link" size={"middle"} href={"./services"}>
              View Services
            </Button>
          </Col>
        </Row>
        <br />
        <br />
        <Row gutter={[60, 60]}>
          <Col sm={11} lg={11}>
            <h2 style={{ textAlign: "left" }}>
              Find a small group that matches your life point.
            </h2>
            <hr align="left" className="thick-line"></hr>
            <p style={{ textAlign: "left" }}>
              What makes church special is not just the event, but also the
              relationships we build that help us stay faithful to God.
              <br />
              <br />
              Whether you’re a single professional, a college student or a
              parent of a special needs child, we have a ministry that we’d love
              for you to be a part of. Check out our ministries page to find a
              small group that you can get connected with.
            </p>
            <Button type="link" size={"middle"} href={"./services"}>
              View Ministries
            </Button>
          </Col>
          <Col sm={11} lg={11}>
            {" "}
            <Image
              width={300}
              height={300}
              src="error"
              fallback="data:image/png;base64,"
            />
          </Col>
        </Row>
        <br />
        <br />
        <Row gutter={[60, 60]}>
          <Col sm={11} lg={11}>
            {" "}
            <Image
              width={300}
              height={300}
              src="error"
              fallback="data:image/png;base64,"
            />
          </Col>
          <Col sm={11} lg={12}>
            <h2 style={{ textAlign: "left" }}>
              Get involved in one of our doing good projects.
            </h2>
            <hr align="left" className="thick-line"></hr>
            <p style={{ textAlign: "left" }}>
              A theme we embrace at the Bay Area Christian Church is “God and
              Good.” We believe it’s important that as Christians we are
              constantly working to do good in the lives of the people in our
              community, regardless of religious affiliation.
              <br />
              <br />
              We’re proud of our members who’ve embraced this call, and are
              inspired by the different “doing good” efforts they’ve championed.
              Check out our volunteer page to learn about how you can get
              involved.
            </p>
            <Button type="link" size={"middle"} href={"./outreach"}>
              Learn More
            </Button>
          </Col>
        </Row>
      </div>
      <div
        style={{
          backgroundColor: "#FFF3E0",
          margin: "auto",
          textAlign: "center",
          display: "flex",
          justifyContent: "center",
          padding: "50px",
        }}
      >
        <Card title="Connect with us" style={{ minWidth: 300 }}>
          <Form
            name="basic"
            initialValues={{
              remember: true,
            }}
          >
            <Form.Item
              label="Name"
              name="Name"
              rules={[
                {
                  required: true,
                  message: "Please input your name!",
                },
              ]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              label="Email"
              name="Email"
              rules={[
                {
                  required: true,
                  message: "Please input your Email!",
                },
              ]}
            >
              <Input />
            </Form.Item>
            <Form.Item
              label="Phone"
              name="phone"
              rules={[
                {
                  message: "Please input your Phone Number!",
                },
              ]}
            >
              <Input />
            </Form.Item>
            <Form.Item
              label="Interest"
              name="interest"
              rules={[
                {
                  message: "Please input your Phone Number!",
                },
              ]}
            >
              <Select
                placeholder="Select a option and change input text above"
                allowClear
              >
                <Option value="service">Join a Service</Option>
                <Option value="event">Attending an Event</Option>
                <Option value="other">other</Option>
              </Select>
            </Form.Item>
            <Form.Item name={["user", "Your message"]} label="How can we help you?">
              <Input.TextArea />
            </Form.Item>

            <Form.Item>
              <Button type="primary" htmlType="submit">
                Submit
              </Button>
            </Form.Item>
          </Form>
        </Card>
      </div>
    </div>
  );
}

export default ImNew;
