import React, { useEffect, useState, useReducer } from "react";
import Banner from "../../components/Banner";

import {
  Card,
  Upload,
  Typography,
  Table,
  Image,
  Space,
  Button,
  Modal,
  Form,
  Input,
} from "antd";

// Similiar to Our History Page
import churchWide from "../../imgs/church-wide.jpg";
import axios from "axios";

function EditVision() {
  const { Title } = Typography;

  const columns = [

    {
      key:"image",
      title: "image",
      key: "image",
      render: (text, record) => (
        <>
          <Image
            width={200}
            src={record.image}
            fallback="data:image/png;base64,"
          />
        </>
      ),
    },
    {
      key:"vision",
      title: "vision",
      dataIndex: "vision",
      key: "vision",
    },
    {
      key:"mission",
      title: "mission",
      dataIndex: "mission",
      key: "mission",
    },

    {
      key:"action",
      title: "Action",
      key: "action",
      render: (text, record) => (
        <Space size="middle">
          <a
            onClick={() => {
              setIsModalVisible(true);
              setRecordToEdit(record);
            }}
          >
            Edit/Delete
          </a>
        </Space>
      ),
    },
  ];
  const [data, setData] = useState();
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [record, setRecordToEdit] = useState({});
  const [imageToUpload, setImageToUpload] = useState();
  const handleOk = () => {
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };
  const onFinish = (values) => {
    console.log("Success:", values);
  };

  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };
  const handleChange = (event) => {
    Object.entries(record).forEach((i) => {
      record[event.target.name] = event.target.value;
      setRecordToEdit(record);
    });
  };
  const onCreateImage = (event) => {
    const MAX_IMAGE_SIZE = 1000000;

    let reader = new FileReader();
    reader.onload = (e) => {
      console.log(e.target);
      console.log("length: ", e.target.result.includes("data:image/jpeg"));
      if (!e.target.result.includes("data:image/jpeg")) {
        return alert("Wrong file type - JPG only.");
      }
      if (e.target.result.length > MAX_IMAGE_SIZE) {
        return alert("Image is loo large.");
      }

      setImageToUpload(e.target.result);

      record["image"] = e.target.result;
      setRecordToEdit(record);
      console.log(record);
    };
    reader.readAsDataURL(event.target.files[0]);
  };

  //API calls

  useEffect(() => {
    try {
      axios.get(`http://localhost:8080/vision`).then((res) => {

        setData(res.data);
      });
    } catch (err) {
      console.log(err);
    }
  }, []);

  const addRecord = () => {
    axios.post(`http://localhost:8080/vision`, {
      vision: "Fill in here",
      mission: "Fill in here",
      image: "Fill in here",

    });
  };
  const saveRecord = () => {
    console.log(record.body);
    axios.put(`http://localhost:8080/vision`, record);
  };
  const deleteRecord = () => {
    axios.delete(`http://localhost:8080/vision`, { data: record });
  };

  return (
    <>
      <Banner name={"Edit Vision Page"} image={churchWide}></Banner>

      <div className="main-container">
        <br />
        <Title style={{ textAlign: "center" }}>Edit Vision Page</Title>
        <hr className="thick-line"></hr>
        <br />
        <br />

        <Table columns={columns} dataSource={data} />
        <Button
          key="Add"
          onClick={() => {
            addRecord();
            window.location.reload();
          }}
        >
          Add Row
        </Button>
        <br />
        {/*Popup modal */}
        <Modal
          title="Basic Modal"
          visible={isModalVisible}
          onOk={handleOk}
          onCancel={handleCancel}
          footer={[
            <Button
              key="cancel"
              onClick={() => {
                handleCancel();
              }}
            >
              Cancel
            </Button>,
            <Button
              key="submit"
              type="primary"
              onClick={() => {
                saveRecord();

                setIsModalVisible(false);
                window.location.reload();
              }}
            >
              Save
            </Button>,
            <Button
              key="delete"
              type="primary"
              danger
              onClick={() => {
                deleteRecord(record);
                window.location.reload();
              }}
            >
              Delete
            </Button>,
          ]}
        >
          <Form
            name="basic"
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
          >

            <Form.Item>
              <input
                type="file"
                name="image"
                onChange={(e) => {
                  onCreateImage(e);
                }}
              />
            </Form.Item>
            <Form.Item
              label="vision"
              name="vision"
              onChange={(e) => handleChange(e)}
              rules={[
                {
                  required: true,
                  message: "Please input the vision!",
                },
              ]}
            >
              {/*Need to change default*/}
              <Input
                name="vision"
                defaultValue={record.vision ? record.vision : ""}
              />
            </Form.Item>
            <Form.Item
              label="mission"
              name="mission"
              onChange={(e) => handleChange(e)}
              rules={[
                {
                  required: true,
                  message: "Please input the mission!",
                },
              ]}
            >
              {/*Need to change default*/}
              <Input
                name="mission"
                defaultValue={record.mission ? record.mission : ""}
              />
            </Form.Item>
          </Form>
        </Modal>
      </div>
    </>
  );
}

export default EditVision;
